export const pick = <T extends object>(
  obj: T,
  keys: (keyof T)[]
): Partial<T> => {
  const res: Partial<T> = {};
  Object.entries(obj || {}).forEach(([key, val]) => {
    if (keys.includes(key as keyof T)) {
      res[key as keyof T] = val;
    }
  });
  return res;
};

export const throttle = <T extends (...args: any[]) => void>(
  callback: T,
  limit: number
) => {
  let wait = false;
  let callAfterWait = false;
  return (...args: Parameters<T>) => {
    if (!wait) {
      callAfterWait = false;
      callback.apply(this, args);
      wait = true;
      setTimeout(() => {
        callAfterWait && callback.apply(this, args);
        wait = false;
      }, limit);
    } else {
      callAfterWait = true;
    }
  };
};

export const debounce = <T extends (...args: any[]) => void>(
  callback: T,
  wait: number
): ((...args: Parameters<T>) => void) => {
  let timeout: NodeJS.Timeout;
  return (...args: Parameters<T>) => {
    clearTimeout(timeout);
    timeout = setTimeout(() => {
      callback.apply(this, args);
    }, wait);
  };
};

export const get = <T>(obj: T, path: string, defaultValue: any): any => {
  const result = String.prototype.split
    .call(path, /[,[\].]+?/)
    .filter(Boolean)
    .reduce(
      (res, key) => (res !== null && res !== undefined ? res[key] : res),
      obj as any // Add an index signature to allow using string as an index
    );
  return result === undefined || result === obj ? defaultValue : result;
};

export const isFunction = (something: any) => typeof something === 'function';

export const isEqual = (obj1: object, obj2: object) => {
  try {
    return JSON.stringify(obj1) === JSON.stringify(obj2);
  } catch (e) {
    return false;
  }
};

export const isNumber = (n: any) => {
  return !isNaN(parseFloat(n)) && isFinite(n);
};
